import { useRef, useState } from "react"
import TawkMessengerReact from "@tawk.to/tawk-messenger-react"
import Bulle from "../svg/contact/Bulle"
import WhatsappSvg from "../svg/contact/Whatsapp"
import EmailSvg from "../svg/contact/Email"
import Chevron from "../svg/contact/Chevron"
import Form from "./Forms/Form"
import QrCode from "../svg/contact/QrCode"

const Cta = () => {
  const [contactIsActive, setContactIsActive] = useState(false)
  const [stateOnglets, setStateOnglets] = useState(1)

  const tawkMessengerRef = useRef()
  const propertyId = "62ceda637b967b1179995bef"
  const widgetId = "1g7s0a5hn"

  const customStyle = {
    visibility: {
      desktop: {
        xOffset: "47",
        yOffset: "134",
        position: "br",
      },

      mobile: {
        xOffset: "80",
        yOffset: "15",
        position: "br",
      },
    },
  }

  const handleOpenContact = () => {
    if (contactIsActive) {
      tawkMessengerRef.current.minimize()
    }

    setContactIsActive(!contactIsActive)
  }
  const handleForm = () => {
    if (stateOnglets === 3) {
      tawkMessengerRef.current.minimize()
    }
    setStateOnglets(1)
  }
  const handleWhatsapp = () => {
    if (stateOnglets === 3) {
      tawkMessengerRef.current.minimize()
    }
    setStateOnglets(2)
  }
  const handleTawkto = () => {
    setStateOnglets(3)
    tawkMessengerRef.current.maximize()
  }

  const hrefs = document.querySelectorAll("a")
  hrefs.forEach((href) => {
    href.addEventListener("click", () => {
      if (tawkMessengerRef.current) {
        tawkMessengerRef.current.minimize()
      }
    })
  })

  window.addEventListener("beforeunload", () => {
    if (tawkMessengerRef.current) {
      tawkMessengerRef.current.minimize()
    }
  })

  const onChatMaximized = () => {
    const iframefirst = document.querySelector(
      "iframe[title='chat widget']:first-of-type"
    )
    iframefirst.style.setProperty("display", "none", "important")
  }
  return (
    <div className="cta">
      <TawkMessengerReact
        propertyId={propertyId}
        widgetId={widgetId}
        ref={tawkMessengerRef}
        // onLoad={onLoad}
        customStyle={customStyle}
        onChatMaximized={onChatMaximized}
      />
      <div
        className={!contactIsActive ? "cta__bubble" : "cta__bubble disabled"}
        onClick={handleOpenContact}
      >
        <button type="button" tabIndex="0" name="Contact">
          <Bulle />
        </button>
      </div>
      <>
        <div
          className={contactIsActive ? "cta__opened" : "cta__opened disabled"}
        >
          <div className="cta__opened__welcome">
            <p className="cta__opened__welcome__title">Bonjour !</p>
            <p className="cta__opened__welcome__p">
              N'hsitez pas  me transmettre :
            </p>
            <ul>
              <li>vos coordonnes, </li>
              <li>la date de votre projet.</li>
            </ul>
            <div className="separating"></div>
            {stateOnglets === 1 ? (
              <Form />
            ) : (
              stateOnglets === 2 && (
                <>
                  <h3>Stphane Abenaqui</h3>
                  <p>Compte professionnel WhatsApp</p>
                  <QrCode />
                </>
              )
            )}
            <div
              className={
                stateOnglets === 3
                  ? "cta__opened__welcome__chatTawkTo"
                  : "cta__opened__welcome__chatTawkTo disabled"
              }
            ></div>
          </div>
          <div className="cta__opened__list">
            <button
              className="cta__opened__email"
              onClick={handleForm}
              type="button"
              tabIndex="0"
              name="Email"
            >
              <EmailSvg />
            </button>
            <button
              className="cta__opened__whatsapp"
              onClick={handleWhatsapp}
              type="button"
              tabIndex="0"
              name="Whatsapp"
            >
              <WhatsappSvg />
            </button>

            <button
              className="cta__opened__tawkto"
              onClick={handleTawkto}
              type="button"
              tabIndex="0"
              name="Tawkto"
            >
              Tawkto
            </button>
          </div>
        </div>
        <div
          className={contactIsActive ? "cta__bubble" : "cta__bubble disabled"}
          onClick={handleOpenContact}
        >
          <button type="button" tabIndex="0" name="Contact">
            <Chevron />
          </button>
        </div>
      </>
    </div>
  )
}

export default Cta
