import React, { useState } from "react"
import Error500 from "./errors/Error500"
import axios from "axios"
const VideoCine = () => {
  const [error, setError] = useState(false)

  const videoID = "707366380"

  //params API
  const srcVideoAPI = "https://api.vimeo.com/videos/"

  //params for Iframe
  const srcVideoIframe = "https://player.vimeo.com/video/"
  const paramsVideo = "?h=2ba2b4dee1&background=1"

  //Just to catch error 500 and load error's component
  axios
    .get(srcVideoAPI + videoID, {
      headers: {
        Authorization: `Bearer ${process.env.REACT_APP_API_TOKEN}`,
      },
    })
    .catch((err) => {
      if (err.response.status === 500) setError(true)
    })
  return (
    <>
      {!error ? (
        <div className="main__wrapper">
          <iframe
            src={srcVideoIframe + videoID + paramsVideo}
            allow="autoplay"
            title="Bandeau"
          ></iframe>
        </div>
      ) : (
        <Error500 />
      )}
    </>
  )
}

export default VideoCine
